import React, { useEffect, useState } from "react";
import styled from "styled-components";

const initialSearch = {
  artist: "",
  song: "",
};

function Form({handleSearch}) {
  const [search, setSearch] = useState(initialSearch);
  useEffect(() => {}, []);

  const handleChange = (e) => {
    setSearch({
      ...search,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!search.artist || !search.song) {
      alert("Uno de los campos esta vacio");
      return;
    }
    else
    {
      handleSearch(search);
    }

    handleReset();
  };

  const handleReset = () => {
    setSearch(initialSearch);
  };

  return (
    <Container>
      <Title>Buscador de Canciones</Title>
      <Formulary>
        <Searcher
          name="artist"
          type="text"
          placeholder="Artista"
          onChange={handleChange}
          value={search.artist}
        ></Searcher>
        <Searcher
          name="song"
          placeholder="Cancion"
          type="text"
          onChange={handleChange}
          value={search.song}
        ></Searcher>
        <Button onClick={handleSubmit}>Buscar</Button>
      </Formulary>
    </Container>
  );
}

export default Form;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 30vh;
  align-items: center;
`;

const Title = styled.h2`
  font-size: 32px;
  font-weight: bold;
  color: 112031;
`;

const Formulary = styled.form`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
  padding: 10px;
  width: 40%;
  font-size: 16px;
  height: auto;
  box-shadow: 0 1px 3px #345b63, 0 1px 2px #345b63;
`;

const Searcher = styled.input`
  width: 35%;
  font-size: 18px;
  outline: none;
  border: none;
  border-bottom: 1px solid #eeeeee;
  padding: 12px 24px 12px 5px;
  color: #112031;
  ::palceholder {
    color: #eeeeee;
  }
  transition: border-color 0.5s ease-in;

  :focus {
    border-color: #112031;
    ::placeholder {
      color: #112031;
    }
  }
`;

const Button = styled.button`
  width: 30%;
  height: 100%;
  outline: none;
  border: none;
  padding: 12px 24px;
  background-color: #345b63;
  color: #d4ecdd;
  font-size: 18px;
  transition: background-color 0.3s;
  :hover {
    background-color: #152d35;
  }
`;
