import React from "react";
import styled from "styled-components";
import "./Song.css";
import Message from "./Message";

function Song({ search, lyric, artist }) {
  if (!lyric && !artist) return null;
  return (
    <Container>
      {(lyric.error || lyric.err || lyric.name === "Abort Error") && artist ? (
        <Message
          msg={`No se encontro la cancion ${search.song} y ni el artista ${search.artist}`}
          bgColor="#911F27"
        />
      ) : (
        <CardContainer>
          <ImgContainer backgroundImg={artist.artists[0].strArtistThumb}>
            <h2>{artist.artists[0].strArtist}</h2>
          </ImgContainer>
          <SongContainer>
            <h3 className="title-song">{search.song}</h3>
            <blockquote className="lyric-song">{lyric.lyrics}</blockquote>
          </SongContainer>
        </CardContainer>
      )}
    </Container>
  );
}

export default Song;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  height: auto;
  align-items: center;
  margin-bottom: 40px;
`;

const CardContainer = styled.div`
  width: 500px;
  height: auto;
  box-shadow: 0 1px 3px #345b63, 0 1px 2px #345b63;
  outline: none;
  border: none;
`;

const ImgContainer = styled.div`
  width: 476px;
  height: 300px;
  display: flex;
  align-items: flex-end;
  color: #ffffff;
  font-weight: bold;
  padding: 24px 12px;
  font-size: 24px;
  color: #d4ecdd;
  background-image: url(${(props) => props.backgroundImg});
  background-repeat: no-repeat;
  background-size: cover;
`;

const SongContainer = styled.div`
  width: 476px;
  height: auto;
  padding: 24px 12px;
`;
